<template>
  <div>
    <v-card class="pb-4" height="60">
      <div class="mt-5 mx-5">
        <template v-if="$auth.check(['ADMIN', 'RESPONSIBLE'])">
          <v-btn class="mr-2 my-2" @click="create()" color="#49c5b6" dark>
            <v-icon left>mdi-doctor</v-icon>
            {{ $vuetify.lang.t("$vuetify.btn.add") }}
          </v-btn>
        </template>
        <v-btn
          :loading="loading.refresh"
          @click="refreshItems()"
          class="mr-2 my-2"
          depressed
          color="#f1f5fc"
        >
          <v-icon left color="#49c5b6">mdi-refresh</v-icon>
          {{ $vuetify.lang.t("$vuetify.btn.refresh") }}
        </v-btn>
        <v-btn
          color="#f1f5fc"
          @click="showFilter()"
          class="mr-2 my-2"
          depressed
        >
          <v-icon color="#49c5b6" left>mdi-filter-outline</v-icon>
          {{ $vuetify.lang.t("$vuetify.doctor.btn.filter.title") }}
        </v-btn>
        <v-btn
          class="font-weight-bold mr-2 my-2"
          @click="exportDoctor()"
          color="teal darken-1"
          dark
          :loading="loadingBtn"
          style="margin-left: 15px"
        >
          <v-icon left>mdi-note-text</v-icon>
          {{ $vuetify.lang.t("$vuetify.doctor.cols.exports.title") }}
        </v-btn>
        <v-spacer></v-spacer>
      </div>
    </v-card>
    <v-data-iterator
      :items="items"
      :items-per-page="itemsPerPage"
      :server-items-length="meta.totalElements"
      :loading="loading.list"
      :options.sync="pagination"
      class="mt-2"
      style="background-color: #ECEFF1"
    >
      <template v-slot:default="props">
        <v-container>
          <v-row>
            <v-col
              v-for="item in props.items"
              :key="item.id"
              cols="12"
              sm="1"
              md="1"
              lg="3"
            >
              <v-card color="transparent">
                <v-divider></v-divider>
                <div style="background-color: white" class="text-center">
                  <v-card-text>
                    <template>
                      <v-avatar class="center" size="75">
                        <v-img :src="require('../../assets/user.png')"></v-img>
                      </v-avatar>
                    </template>
                    <div
                      class="text-center font-weight-bold black--text mt-5"
                      style="font-size: 15px"
                    >
                      {{ item.name.toUpperCase() }}
                    </div>
                    <div
                      class="text-center grey--text text--darken-1 subtitle-1 mt-3"
                      style="font-size: 15px"
                    >
                      {{ item.speciality }}
                    </div>
                    <div
                      class="font-weight-bold black--text mt-3"
                      style="font-size: 15px"
                    >
                      {{ item.phone }}
                    </div>
                    <div
                      class="text-center font-weight-bold black--text mt-3"
                      style="font-size: 15px"
                    >
                      {{ item.address }}
                    </div>
                  </v-card-text>
                  <v-card-text class="text-center">
                    <v-menu bottom transition="scale-transition">
                      <template v-slot:activator="{ on }">
                        <v-btn icon v-on="on">
                          <v-icon size="30" width="2px">mdi-menu</v-icon>
                        </v-btn>
                      </template>
                      <v-list class="menu-app-bar">
                        <v-list-item @click="editUser(item)">
                          <v-list-item-title>
                            {{ $vuetify.lang.t("$vuetify.btn.detail") }}/{{
                              $vuetify.lang.t("$vuetify.btn.edit")
                            }}
                          </v-list-item-title> </v-list-item
                        ><v-list-item @click="deleteItem(item)">
                          <v-list-item-title>
                            {{ $vuetify.lang.t("$vuetify.btn.remove") }}
                          </v-list-item-title>
                        </v-list-item>
                      </v-list>
                    </v-menu>
                  </v-card-text>
                </div>
              </v-card>
            </v-col>
          </v-row>
        </v-container>
      </template>
    </v-data-iterator>
    <form-doctor
      :dialog="dialogForm"
      :form="form"
      :messages="messages"
      @getItems="getItems"
      ref="userForm"
    ></form-doctor>
    <delete-item
      :dialog="dialogDelete"
      :messages="messagesDelete"
      :urlItems="urlDeleteItems"
      @getItems="getItems"
    ></delete-item>
    <filter-form
      ref="filterForm"
      :dialog="dialogForm"
      :filter="filter"
      @performFilter="performFilter"
    ></filter-form>
  </div>
</template>

<script>
import ListMixin from "./../../mixins/Common/List.vue";
import FormDoctor from "./Form";
import FilterForm from "./Filter";
import DeleteItem from "./../Common/Delete";
const FileSaver = require("file-saver");
export default {
  mixins: [ListMixin],
  data: () => ({
    itemsPerPage: 10,
    urlItems: "/doctors",
    sortBy: ["id"],
    dialogForm: {
      show: false,
      shows: {
        showFilter: false,
        showInfo: false
      }
    },
    dialogDelete: {
      show: false
    },
    dialogDetail: {
      show: false
    },
    dialogEnt: {
      show: false
    },
    loadingBtn: false,
    form: {
      id: null,
      code_internal: null,
      coef: null,
      dateNaiss: null,
      dateService: null,
      intervention_days: null,
      position: null,
      address: null,
      speciality: null,
      phone: null,
      email: null,
      department: null
    },
    filter: {
      username: null,
      role: null
    },
    messages: {
      title: "",
      submit: "",
      success: ""
    },
    messagesDelete: {
      success: "$vuetify.doctor.delete.success"
    }
  }),
  methods: {
    exportDoctor() {
      let self = this;
      self.$store
        .dispatch("request", {
          url: self.urlItems + "/" + "exports",
          responseType: "blob"
        })
        .then(response => {
          let fileName = "Listes des doctors";
          let blob = new Blob([response.data], { type: response.data.type });
          console.log(response.data);
          FileSaver.saveAs(blob, fileName);
        });
    },
    setHeaders() {
      this.headers = [
        {
          text: "$vuetify.doctor.cols.code_internal.title",
          value: "code",
          align: "start",
          sortable: false,
          width: "15",
          class: "grey--text text--darken-3"
        },
        {
          text: "$vuetify.doctor.cols.first_name.title",
          value: "first_name",
          align: "start",
          sortable: false,
          width: "50",
          class: "grey--text text--darken-3"
        },
        {
          text: "$vuetify.departments.title",
          value: "departments",
          align: "start",
          sortable: false,
          width: "50",
          class: "grey--text text--darken-3"
        },
        {
          text: "$vuetify.doctor.cols.speciality.title",
          value: "speciality",
          align: "start",
          sortable: false,
          width: "20",
          class: "grey--text text--darken-3"
        },
        {
          text: "$vuetify.doctor.cols.phone.title",
          value: "phone",
          align: "start",
          sortable: false,
          width: "20",
          class: "grey--text text--darken-3"
        },
        {
          text: "$vuetify.doctor.cols.position.title",
          value: "position",
          align: "start",
          sortable: false,
          width: "30",
          class: "grey--text text--darken-3"
        },

        {
          text: "$vuetify.doctor.cols.created.title",
          value: "create_date",
          align: "start",
          sortable: false,
          width: "105",
          class: "grey--text text--darken-3"
        },
        {
          text: "$vuetify.doctor.cols.actions.title",
          value: null,
          align: "start",
          sortable: false,
          width: "140"
        }
      ];
    },
    create() {
      this.dialogForm.show = true;
      this.messages = {
        title: "$vuetify.doctor.new.title",
        submit: "$vuetify.doctor.new.submit",
        success: "$vuetify.doctor.new.success"
      };
      this.form = {
        id: null,
        coef: null,
        code_internal: null,
        dateNaiss: null,
        dateService: null,
        intervention_days: null,
        position: null,
        address: null,
        speciality: null,
        name: null,
        phone: null,
        email: null,
        username: null,
        password: null,
        re_password: null,
        role: null,
        is_active: null
      };
    },
    editUser(item) {
      this.dialogForm.show = true;
      this.messages = {
        title: "$vuetify.doctor.update.title",
        submit: "$vuetify.doctor.update.submit",
        success: "$vuetify.doctor.update.success"
      };
      this.$refs.userForm.setForm(item);
    },
    enabled(user) {
      this.$refs.enableUser.setForm(user);
      if (!user.is_active) {
        this.dialogForm.shows.showInfo = true;
        this.messages = {
          title: "$vuetify.doctor.cols.status.enabled",
          submit: "$vuetify.doctor.update.submit",
          success: "$vuetify.doctor.update.success"
        };
      } else {
        this.dialogForm.shows.showInfo = true;
        this.messages = {
          title: "$vuetify.doctor.cols.status.desabled",
          submit: "$vuetify.doctor.update.submit",
          success: "$vuetify.doctor.update.success"
        };
      }
    },
    showFilter() {
      this.dialogForm.shows.showFilter = true;
    },
    performFilter() {
      let self = this;
      self.loading.list = true;
      let params = {};
      if (self.filter.username !== null) {
        params["username"] = self.filter.username;
      }
      if (self.filter.role !== null) {
        params["role"] = self.filter.role;
      }
      self._search = params;
      //console.log("FILTRE", this._search);
      self
        .getItems()
        .then(() => {
          self.$refs.filterForm.closeDialog();
        })
        .finally(() => {
          self.$refs.filterForm.stopLoadingBtn();
        });
    },
    assignEnterprise(item) {
      this.dialogEnt.show = true;
      this.$refs.assignForm.setForm(item);
    }
  },
  components: {
    FormDoctor,
    DeleteItem,
    FilterForm
  }
};
</script>

<style scoped>
.call-button-container {
  margin: auto;
  width: 170px;
  vertical-align: middle;
  padding: 10px;
  -webkit-border-radius: 4px;
  -moz-border-radius: 4px;
  border-radius: 4px;
  font-family: lato-bold;
  font-size: 17px;
  color: white;
}
</style>
